@charset "utf-8";

.todo-list-template{
  background: #fff;
  width: 600px;
  margin: 0 auto;
  margin-top: 3rem;
  box-shadow: 0px 3px 6px #ccc;
}
.title{
  padding: 2rem;
  font-size: 1.2rem;
  text-align: center;
  font-weight: bold;
  background: #d32b9b;
  color: #fff;
}
.form-wrapper{
  padding: 1rem;
  border-bottom: 1px solid #d32b9b;
}
.todos-wrapper{
  min-height: 5rem;
}