@charset "utf-8";

.todo-item{
padding: 1rem;
display: flex;
align-items: center;
cursor: pointer;
transition: 0.5s;
user-select: none;
}
.todo-item:hover{
  background: #ccc;
}
.todo-item:hover .remove{
  opacity: 1;
}
.todo-item + .todo-item{
  border-top: 1px solid #f1f3f5;
}
.remove{
  margin-right: 1rem;
  color: #e64980;
  font-weight: bold;
  opacity: 0;
}
.todo-text{
  flex: 1;
  word-break: break-all;
}
.checked{
  text-decoration: line-through;
  color: #d32b9b;
}
.check-mark{
font-size: 1.5rem;
line-height: 100%;
margin-left: 1rem;
color: #d32b9b;
font-weight: bold;
}
