@charset "utf-8";

.form{display: flex;}

.form input{
  flex: 1;
  font-size: 1.25rem;
  outline: none;
  border: none;
  border-bottom: 1px solid #ecc7e0;
}
.create-button{
  padding: 0.5rem;
  margin-left: 1rem;
  background: #d32b9b;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  border-radius: 3px;
}
.create-button:hover{
  background: #d656ab;
}
